import React from 'react';
import { Link } from "react-router-dom";
import styles from '../styles/Projects.module.css';

export default class Projects extends React.Component {
  render() {
    const projects = this.props.projects;
    return (
      <div className="fadediv flex-container">
        <div className={styles.content}>
          <h2>Project Portfolio</h2>
          <p>Some of the things I've worked on in my free-time, my coursework, and of course collaborations with others. I've developed some exciting projects that I've been very passionate about! I am actively working on projects so check back for updates in the future!</p>
          <div className={styles.grid}>
            {projects.map((project, index) => (
              <div key={index} className="hvr-grow imageWrapper">
                <Link to={"/projects/" + project.name}>
                  <img title={project.title + " / " + project.technologies} className="image" src={project.imgIcon} alt={project.name}/>
                </Link>
              </div>
            ))}
          </div>
        </div>
      </div>
    )
  }
}
